@import 'common/utils/colours';

.input {
  margin-top: 20px;
  display: block;
  clear: both;
  position: relative;
  color: #000000;

  &__title {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    clear: both;
    display: block;
  }

  &__warning {
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 20px;

    &--icon,
    &--text {
      display: inline-block;
      max-width: calc(100% - 29px);
    }

    &--icon {
      svg {
        color: #cd1732;
      }
    }

    &--text {
      color: #000000;
      text-align: left;
      top: -2px;
      position: relative;
      margin-left: 12px;
    }
  }

  &__element {
    &--clear {
      position: absolute;
      right: 15px;
      top: 24px;
      z-index: 1;
      cursor: pointer;
    }

    &--text,
    &--select {
      background-color: white;
      margin-top: 10px;
      height: 45px;
      border: 2px solid #000000;
      box-sizing: border-box;
      width: 100%;
      padding-left: 10px;
      padding-right: 10px;
    }

    &--select:disabled {
      border: 2px solid #aaaaaa;
    }
  }
}
