.desktop {
  .create-page {
    width: 480px;
  }
}

.mobile-tablet {
  .create-page {
    &__content {
      margin-bottom: 100px;
    }

    &__footer {
      position: fixed;
      bottom: 0px;
      width: calc(100vw - 40px);
      left: 0px;
      padding: 20px;
      background: white;
      background: linear-gradient(0deg, white 86%, #ffffff5e 100%);
    }
  }
}

.create-page {
  text-align: left;

  &__title {
    text-align: left;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-transform: uppercase;
    color: #000000;
  }
}
